import React from "react";

const Header = () => {
  const show = () => {
    alert(
      "Pomodoro timer is a useful technique used for improving your focus."
    );
  };
  return (
    <div className="navigation">
      <a onClick={show} className="btn btn--nav">
        What is this?
      </a>
      <a onClick={show} className="btn btn--nav">
        How to use
      </a>
    </div>
  );
};

export default Header;



