import React, { Component } from "react";
import Timer from "./components/Timer";
import editableTitle from "./components/Title";
import logo from './time.svg'
// import Controls from "./components/Controls";

let EditableTitle = editableTitle('Pomo');
const Logo = require('./time.svg')
class App extends Component {
  state = {
    onBreak: false
  };
  changeBreak = () => {
    this.setState({
      onBreak: !this.state.onBreak
    })
  }
  resetBreak = () => {
    this.setState({
      onBreak: false
    })
  }
  onChange = e => {
    this.setState({
      value: e.target.value
    });
  };
  render() {
    return (
      <div>
        <div className={!this.state.onBreak ? "dashboard" : "dashboard dashboard--break"}>
          <img
            className="image"
            src={logo}
            alt="tomato"
          />
          {/* <input
            onChange={this.onChange}
            value={this.state.value}
            type="text"
            placeholder={this.state.title}
          /> */}
          <EditableTitle value="My Pomodoro Timer"/>
          <Timer
          changeBreak={this.changeBreak}
          onBreak={this.state.onBreak}
          resetBreak={this.resetBreak}
          />
        </div>
      </div>
    );
  }
}

export default App;



